	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Camping","date":"02 Jun 2006","slug":"/2006-06-02-camping/","tags":"Y2006"},"html":"<p><a href=\"http://www.ratzeburg.de/index.phtml?La=1&amp;object=tx|271.2.1\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2006/06//ratzeburg_seeen.png\" border=\"0\" height=\"120\" width=\"225\" alt=\"ratzeburg_seeen.png\" align=\"right\" /></a>We're taking Christopher for his first camping trip this weekend. We're heading to <a href=\"http://www.ratzeburg.de/\">Ratzeburg</a> and meeting up with his uncles and cousins for the <a href=\"http://en.wikipedia.org/wiki/Pentecost\">Pentecost</a> (or is it <a href=\"http://en.wikipedia.org/wiki/Whitsun\">Whitsun</a>) holiday weekend.</p>\n<p>We haven't been camping for a few years, so we've been hectically gathering supplies and wondering what we're missing. I'm sure we'll realize we've forgotten something essential once we are there. That's part of of the fun, and it's not like we're in the wilderness. The shops are open all day Saturday. </p>\n<p>Just to complicate matters, MamaMaus comes home late from a business meeting tonight, so Christopher and I are packing everything into the Lupo this afternoon, and Mama will be joining us tomorrow in the A8 (did I mention she's now driving Audi after over a decade of BMW?). </p>\n<p>Christopher is very excited. We're just hoping the weather will turn relatively dry.</p>"},"next":{"fields":{"title":"Ancient History","date":"27 May 2006","slug":"/2006-05-27-ancient-history/"}},"previous":{"fields":{"title":"MB 13/1.83 CTO WHITE","date":"02 Jun 2006","slug":"/2006-06-02-mb-13183-cto-white/"}}},{"node":{"fields":{"title":"Ancient History","date":"27 May 2006","slug":"/2006-05-27-ancient-history/","tags":"Y2006"},"html":"<p>MamaMaus dug out some old photos for a presentation she's giving at her old <em>Gymnasium </em>(high school). Here's how she looked when I fell in love with her.</p>\n<p><a href=\"http://www.flickr.com/photos/papascott/154267865/\" title=\"Photo Sharing\"><img src=\"http://static.flickr.com/57/154267865_d8972ec34f.jpg\" width=\"500\" height=\"338\" alt=\"oakgrove2\" /></a></p>\n<p>I still get that smile sometimes. Also note the original Macintosh computer at the lower left.</p>\n<p><a href=\"http://www.flickr.com/photos/papascott/154265947/\" title=\"Photo Sharing\"><img src=\"http://static.flickr.com/49/154265947_089e569488.jpg\" width=\"500\" height=\"339\" alt=\"oakgrove1\" /></a></p>\n<p>A political statement. Both photos must be in our first apartment in 1983.</p>\n<p><a href=\"http://www.flickr.com/photos/papascott/154272355/\" title=\"Photo Sharing\"><img src=\"http://static.flickr.com/70/154272355_bf474e70cb.jpg\" width=\"500\" height=\"420\" alt=\"gopherhole82\" /></a></p>\n<p>This is me, January 1981 in Minnesota. Almost no-shirt weather. </p>"},"next":{"fields":{"title":"Ancient Documents","date":"27 May 2006","slug":"/2006-05-27-ancient-documents/"}},"previous":{"fields":{"title":"Camping","date":"02 Jun 2006","slug":"/2006-06-02-camping/"}}},{"node":{"fields":{"title":"Ancient Documents","date":"27 May 2006","slug":"/2006-05-27-ancient-documents/","tags":"Y2006"},"html":"<p>We were digging through <a href=\"http://www.papascott.de/archives/2006/05/27/ancient-history/\">ancient artifacts</a> to find this article by MamaMaus from her student newspaper, written as an exchange student in spring 1980. It gives her impression of the American school system. No carbon dating is necessary due to the reference to Ronald Reagan as \"likely Republican candidate\". <a href=\"http://www.flickr.com/photo_zoom.gne?id=154297091&size=o\" title=\"Photo Sharing\">Click</a> to enlarge.</p>\n<p><a href=\"http://www.flickr.com/photo_zoom.gne?id=154297091&size=o\" title=\"Photo Sharing\"><img src=\"http://static.flickr.com/69/154297091_38387d7e61.jpg\" width=\"404\" height=\"500\" alt=\"nebelhorn_artz\" /></a></p>"},"next":{"fields":{"title":"Harry Potter and the Development of Christopher","date":"19 May 2006","slug":"/2006-05-19-harry-potter-and-the-development-of-christopher/"}},"previous":{"fields":{"title":"Ancient History","date":"27 May 2006","slug":"/2006-05-27-ancient-history/"}}}],"pathPrefix":"","first":false,"last":false,"index":216,"pageCount":1101}}

